        var eYear = endTile.getAttribute('year').toNumber();
        var eMonth = Math.max(Number(endTile.getAttribute('month')) - 1, 0);
        var eDate = endTile.getAttribute('date').toNumber();
        event.start = startNum;

        var originStartDate = new Date(event.startDate);
        var originEndDate = new Date(event.endDate);

        var nextStartDate = new Date(sYear, sMonth, sDate);

        nextStartDate.setHours(originStartDate.getHours());
