        var nextEndDate = new Date(eYear, eMonth, eDate);
        nextEndDate.setHours(originEndDate.getHours());
        nextEndDate.setMinutes(originEndDate.getMinutes());

        event.startDate = nextStartDate;
        event.endDate = nextEndDate;
        return event;
